import Notification from '../../../components/notification/src/notification';

const TYPE_TITLE = {
    'success': '',
    'info': '',
    'warning': '',
    'negative': '',
};

export default {
    open: open
}

function open(type, message, duration) {
    if (!Notification) return;

    let options = {
        type: type,
        title: TYPE_TITLE[type],
        message: message,
        duration: duration ? duration : 2000
    };

    Notification(options);
}


