import Login from './views/login.vue';
import Layout from './views/layout.vue';
import Home from './views/home.vue';
import OrderList from './views/order/order-list.vue';

export default {
    routes: [
        {
            path: '/',
            redirect: to => {
                return '/app/home';
            }
        },
        {
            name: 'login',
            path: '/login',
            component: Login
        },
        {
            name: 'app',
            path: '/app',
            component: Layout,
            children: [
                {
                    name: 'app.home',
                    path: '/app/home',
                    component: Home
                },
                {
                    name: 'app.order.list',
                    path: '/app/order/list',
                    component: OrderList
                }
            ]
        },
    ]
}


