export default {
    setLocalStorage: setLocalStorage,
    getLocalStorage: getLocalStorage,
    removeLocalStorage: removeLocalStorage,
    setSessionStorage: setSessionStorage,
    getSessionStorage: getSessionStorage,
    removeSessionStorage: removeSessionStorage,
}

/**
 * localStroage
 * @param {string} key
 * @param {any} value
 * @param {boolean} isStringify
 */
function setLocalStorage(key, value, isStringify) {
    //	localStorage
    if (!storageAvailable('localStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    window.localStorage.setItem(key, isStringify ? JSON.stringify(value) : value);
}

/**
 * localStorage
 * @param {string}  key
 * @param {boolean} isParse
 * @return {object}
 */
function getLocalStorage(key, isParse) {
    //	localStorage
    if (!storageAvailable('localStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    return isParse ? JSON.parse(window.localStorage.getItem(key)) : window.localStorage.getItem(key);
}

/**
 * localStorage
 * @param {string} key
 */
function removeLocalStorage(key) {
    //	localStorage
    if (!storageAvailable('localStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    window.localStorage.removeItem(key);
}

/**
 * sessionStorage
 * @param {string} key
 * @param {any} value
 * @param {boolean} isStringify
 */
function setSessionStorage(key, value, isStringify) {
    //	sessionStorage
    if (!storageAvailable('sessionStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    window.sessionStorage.setItem(key, isStringify ? JSON.stringify(value) : value);
}

/**
 * sessionStorage
 * @param {string} key
 * @param {boolean} isParse
 * @return {object}
 */
function getSessionStorage(key, isParse) {
    //	sessionStorage
    if (!storageAvailable('sessionStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    return isParse ? JSON.parse(window.sessionStorage.getItem(key)) : window.sessionStorage.getItem(key);
}

/**
 * sessionStorage
 * @param  {string} key
 */
function removeSessionStorage(key) {
    //	sessionStorage
    if (!storageAvailable('sessionStorage')) {
        return;
    }

    //	keynull, undefined, 0, false
    if (!key) {
        return;
    }

    window.sessionStorage.removeItem(key);
}

/**
 * localStoragesessionStorage
 * @param {string} type
 */
function storageAvailable(type) {
    try {

        let storage = window[type],
            x = '__storage_test_';

        storage.setItem(x, x);
        storage.removeItem(x);
        return true;

    } catch (error) {
        console.error('localStoragesessionStorage');

        return false;
    }
}



